import React from 'react';
import CardHierOrder from './CardHook'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import BodyMpbile from './bodyMobile'
import BodyDesktop from './bodyDesktop'
import './Card.css'
import '../../Css/default_card_designs.css'
import '../../Css/Templet/Templets.css'


class Card extends React.Component {
    constructor(props) {
        super(props);

    }

    render() {
        const { showState, cssObj,changeCoverStyle,changeProfileStyle,changeLogoStyle, setvCardStyleToggle, screenSize, card,setImgModalShow,setImgId,setFunc,preview, screenSizep } = this.props;
        return (
            <>
                <div className={cssObj.cssObjTamplate.cover} id={showState && !preview ? "cover_prevwie" : "cover"} title="Add Img"
                    style={cssObj ? cssObj.cover ? cssObj.cover : null : null}
                    onClick={!showState ? () => {setImgModalShow(true);setImgId("#fileLoaderCover");setFunc(changeCoverStyle);setvCardStyleToggle() } : ""}
                >
                </div>
                <div className={cssObj.cssObjTamplate.logo} id={showState || preview ? "logo_prevwie" : "logo"}
                    style={cssObj ? cssObj.logo ? cssObj.logo : null : null}
                    onClick={!showState ? () => {setImgModalShow(true);setImgId("#fileLoaderLogo");setFunc(changeLogoStyle);setvCardStyleToggle() } : ""}
                    title="Add Img"
                >
                </div>
                <div  style={{ width: "58px", height: "58px",  top: `calc(-130px)`, left: `calc(50% - ${"-20px"})`,position: "relative", zIndex: 1}}>
                <div>
                    {card.currentCard.vCardStyle.profileShow ? <div
                       className={cssObj.cssObjTamplate.profile} id="profile"
                        style={cssObj ? cssObj.profile ? cssObj.profile : null : null}
                        onClick={!showState ? () => {setImgModalShow(true);setImgId("#fileLoaderProfile");setFunc(changeProfileStyle);setvCardStyleToggle() } : ""}
                        title="Add Img"
                    >
                    </div> : ""}
                </div>
                </div>


                {/* {card.currentCard.vCardStyle.profileShow ? <div
                            className="defult_card_profile_modern" id="profile"
                            style={cssObj ? cssObj.profile ? cssObj.profile : null : null}
                            onClick={!showState?(e) => { changeImg(e, "#fileLoaderProfile") }:""}
                        >
                        </div>:""} */}
                {!showState ? screenSize === 0 ? <BodyMpbile {...this.props} ></BodyMpbile>
                    : <BodyDesktop {...this.props} ></BodyDesktop> :
                    preview ? screenSizep === 0 ? <BodyMpbile {...this.props} ></BodyMpbile>
                        : <BodyDesktop {...this.props} ></BodyDesktop> :
                        <>
                            <div className="d-md-none ">
                                <BodyMpbile className="d-md-none " {...this.props}></BodyMpbile>
                            </div>
                            <div className="d-none d-md-block d-lg-block"><BodyDesktop {...this.props} ></BodyDesktop></div>
                        </>}
                {/* <img src={require('../../img/ATARA.jpg')}/> */}
            </>
        );
    }
}
export default CardHierOrder(Card);