import React from 'react';
import SocialMediaCard from './SocialMediaCard'
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import Reveiws from '../Reveiws/Reveiws'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import './Card.css'
import '../../Css/default_card_designs.css'
import '../TamplateCard/tamplate.css'
import AlertDelete from '../alert/AlertDelete';
import ImageGridList from '../Gallery/GridGallery'
import SlideGallery from '../Gallery/SlideGallery'
import ControlledAccordions from './t'


class BodyMpbile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            openTextAboutMe: false,
            openTextAboutUs: false,
            aboutStatus: 0,//0-close 1-aboutMe 2-aboutUs
            // footerSendEmailShow: false

        };
    }

    scrolToFotter = () => {
        if (this.props.preview) {
            document.getElementsByClassName("mobile")[1].scrollBy(0, 1000);
        }
        else {
            if (document.getElementsByClassName("mobile").length > 0)
                document.getElementsByClassName("mobile")[0].scrollBy(0, 1000);
            else if (document.getElementById("View"))
                document.getElementById("View").scrollBy(0, 1000);
        }
        this.props.setFooterStyleToggle();

    }

    handleChange = (panel) => (event, isExpanded) => {
        this.props.setExpanded({ isExpanded: isExpanded, panel: panel });
    };
    // bodyCardMobile
    render() {
        // var num = this.props.card.currentCard.numSocialMedia
        var num = 10, rows = [];
        const { showState, cssObj,
            agentDetails, card, gallery, changeFullName, changeRule, setAgentDetailsToggle, setAbouttitle,
            setSocialMediaToggle, setvCardStyleToggle, setFooterStyleToggle, setGalleryStyleToggle, setReveiwsTitle,
            preview, setReveiwsToggle, setGallerytitle, reveiw, setReveiwShow } = this.props;

        return (
            <>
                <div className="row defult_card_body_modern "
                    style={cssObj ? cssObj.bodyCardMobile.body_card ? cssObj.bodyCardMobile.body_card : null : null}
                    id={showState && !preview ? "body_mobile_preview" : "body_mobile"}
                    onClick={setvCardStyleToggle}
                    title={showState ? "" : "Change body color"}
                >

                    <div className="col d-flex flex-column p-0" style={{ background: cssObj.bodyCardMobile.body_card.background, marginTop: cssObj.bodyCardMobile.body_card.body_content_top }}>
                        {!showState ? <> <input
                            autoComplete="new-password"
                            className="row p-0 input_card"
                            placeholder={agentDetails.fullName !== "" ? "" : "Name"}
                            value={agentDetails.fullName !== "" ? agentDetails.fullName : "Name"}
                            onChange={(e) => { changeFullName(e.target.value) }}
                            onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                            hidden={!card.currentCard.vCardStyle.fullNameShow}
                            style={cssObj ? cssObj.bodyCardMobile.input_card_fullName ? cssObj.bodyCardMobile.input_card_fullName : null : null}
                            title={showState ? "" : "Change name"}
                            id={showState ? "fullName_p" : "fullName"}
                        ></input>
                            <input
                                autoComplete="new-password"
                                className="row m-0 p-0 input_card "
                                placeholder={agentDetails.rule !== "" ? "" : "Role"}
                                value={agentDetails.rule !== "" ? agentDetails.rule : "Role"}
                                onChange={(e) => { changeRule(e.target.value) }}
                                onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                hidden={!card.currentCard.vCardStyle.ruleShow}
                                style={cssObj ? cssObj.bodyCardMobile.input_card_rule ? cssObj.bodyCardMobile.input_card_rule : null : null}
                                title={showState ? "" : "Change role"}
                                id={showState ? "role_p" : "role"}
                                autoComplete="new-password"
                            ></input></> : <>
                                {agentDetails.fullName !== "" ? <label className="row  p-0 input_card"
                                    hidden={!card.currentCard.vCardStyle.fullNameShow}
                                    style={cssObj ? cssObj.bodyCardMobile.input_card_fullName ? cssObj.bodyCardMobile.input_card_fullName : null : null}
                                >{agentDetails.fullName} </label> : ""}
                                {agentDetails.rule !== "" ? <label className="row  p-0 input_card"
                                    hidden={!card.currentCard.vCardStyle.ruleShow}
                                    style={cssObj ? cssObj.bodyCardMobile.input_card_rule ? cssObj.bodyCardMobile.input_card_rule : null : null}
                                >{agentDetails.rule}</label> : ""}</>
                        }
                        <div className={cssObj ? cssObj.bodyCardMobile.socialMediaSpace.bodySpaceCss ? cssObj.bodyCardMobile.socialMediaSpace.bodySpaceCss : "" : ""} style={cssObj ? cssObj.socialMediaSpace ? cssObj.socialMediaSpace : null : null} >
                            <SocialMediaCard
                                css={cssObj ? cssObj.bodyCardMobile.socialMediaSpace ? cssObj.bodyCardMobile.socialMediaSpace.css : "col-4 mt-2" : "col-4 mt-2"}
                                showState={showState}
                                onClickFunc={!showState ? setAgentDetailsToggle : null}
                                styleIcon={cssObj ? cssObj.bodyCardMobile.socialMedia ? cssObj.bodyCardMobile.socialMedia : null : null}
                                title={"Call me"}
                                icon={"FaPhoneAlt"}
                                url={`tel:+${card.currentCard.agentDetails.mobile}`}
                            ></SocialMediaCard>
                            <SocialMediaCard
                                id="mail"
                                css={cssObj ? cssObj.bodyCardMobile.socialMediaSpace ? cssObj.bodyCardMobile.socialMediaSpace.css : "col-4 mt-2" : "col-4 mt-2"}
                                showState={showState}
                                onClickFunc={!showState ? setAgentDetailsToggle : null}
                                styleIcon={cssObj ? cssObj.bodyCardMobile.socialMedia ? cssObj.bodyCardMobile.socialMedia : null : null}
                                title={"Mail"}
                                icon={"FaPaperPlane"}
                                // url={`mailto:${card.currentCard.agentDetails.personalEmail}`}
                                url={''}
                                setFooterSendEmailShowMobile={this.scrolToFotter}
                            ></SocialMediaCard>

                            {
                                card.currentCard.socialMedias.map((socialMedia, i) => {
                                    if (socialMedia.show === true) {
                                        num -= 1;
                                        return <SocialMediaCard
                                            key={i}
                                            showState={showState}
                                            css={cssObj ? cssObj.bodyCardMobile.socialMediaSpace ? cssObj.bodyCardMobile.socialMediaSpace.css : "col-4 mt-2" : "col-4 mt-2"}
                                            onClickFunc={!showState ? setSocialMediaToggle : null}
                                            styleIcon={cssObj ? cssObj.bodyCardMobile.socialMedia ? cssObj.bodyCardMobile.socialMedia : null : null}
                                            title={socialMedia.name}
                                            icon={socialMedia.icon}
                                            url={socialMedia.url}
                                        ></SocialMediaCard>

                                    }
                                    return "";
                                }
                                )

                            }
                            {num > 0 && !showState ?
                                <SocialMediaCard
                                    css={cssObj ? cssObj.bodyCardMobile.socialMediaSpace ? cssObj.bodyCardMobile.socialMediaSpace.css : "col-4 mt-2" : "col-4 mt-2"}
                                    showState={showState}
                                    onClickFunc={!showState ? setSocialMediaToggle : null}
                                    styleIcon={cssObj ? cssObj.bodyCardMobile.socialMedia ? cssObj.bodyCardMobile.socialMedia : null : null}
                                    title={"Plus"}
                                    icon={"FaPlus"}
                                    url={``}
                                ></SocialMediaCard>
                                : ""}
                            {rows.map((val) => { return val; })}
                        </div>
                        {/* abouts */}
                        <div className="p-1 mt-3" style={{ width: "100%" }} onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle(); }}>
                            {card.currentCard.vCardStyle.aboutShow ? <Accordion style={cssObj.bodyCardMobile.input_about_card   }
                                expanded={card.expanded === 'about'}
                                onChange={this.handleChange('about')}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                                    aria-controls="aboutbh-content"
                                    id="aboutbh-header"
                                >
                                    <Typography>
                                        {showState ? agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About" :
                                            <input
                                                className={cssObj.cssObjTamplate.input_about_templet}
                                                onChange={(e) => { setAbouttitle(e.target.value) }}
                                                title={showState ? "" : "Change about title"}
                                                value={agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About my business"}
                                                placeholder="About my business"
                                                id={showState ? "about_p" : "about"}
                                            ></input>}
                                    </Typography>
                                </AccordionSummary>
                                <AccordionDetails className="rounded" style={{ background: "transperent", color: "black" }}>
                                    <Typography style={{ width: "100%" }}>
                                        {!showState ? <textarea
                                            title="Write about yourself or your business"
                                            className="about_card"
                                            onChange={(e) => { e.stopPropagation(); this.props.setAboutConnect(e.target.value) }}
                                            value={this.props.agentDetails.aboutConnect !== "" ? this.props.agentDetails.aboutConnect
                                                : ""}
                                            placeholder={"I'm a product designer based in Tel-Aviv with experience in delivering end-to-end UX/UI design for software products."}
                                        >
                                        </textarea > : <div className="about_card"> {this.props.agentDetails.aboutConnect}</div>}
                                    </Typography>
                                </AccordionDetails>
                            </Accordion> : ""}
                            {/* Gallery */}
                            {gallery && gallery.galleryShow ?
                                <Accordion style={cssObj.bodyCardMobile.input_about_card   }
                                    expanded={card.expanded === 'gallery'}
                                    onChange={this.handleChange('gallery')}
                                    onClick={(e) => { e.stopPropagation(); setGalleryStyleToggle() }}
                                >

                                    <AccordionSummary
                                        expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                                        aria-controls="gallerybh-content"
                                        id="gallerybh-header"
                                    >
                                        <Typography>
                                            {showState ? agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "Gallery" :
                                                <input

                                                    className={cssObj.cssObjTamplate.input_about_templet}
                                                    onChange={(e) => { setGallerytitle(e.target.value) }}
                                                    title={showState ? "" : "Change Gallery title"}
                                                    value={gallery.title !== "" ? gallery.title : ""}
                                                    placeholder="Gallery"
                                                    id={showState ? "about_p" : "about"}
                                                ></input>}
                                        </Typography>
                                    </AccordionSummary>
                                    <AccordionDetails className="rounded" style={{ background: "transperent", color: "black", width: "100%" }}>
                                        <Typography component={'span'} style={{ width: "100%" }}>
                                            <div className="row mt-3  d-flex flex-column p-2">
                                                {gallery.state === 'grid' ? <ImageGridList {...this.props}></ImageGridList> : <SlideGallery {...this.props} ></SlideGallery>}
                                            </div>
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion> : ""}

                            {/* Reveiws */}
                            {reveiw.reveiwShow ?
                                <Accordion style={cssObj.bodyCardMobile.input_about_card }
                                    expanded={card.expanded === 'reveiws'}
                                    onChange={this.handleChange('reveiws')}
                                    onClick={(e) => { e.stopPropagation(); setReveiwsToggle(); }}
                                >

                                    <AccordionSummary
                                        expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                                        aria-controls="reveiws-content"
                                        id="reveiwsbh-header"
                                    >
                                        <Typography>
                                            {showState ? reveiw.titleReveiws !== "" ? reveiw.titleReveiws : "Reveiws" :
                                                <input

                                                    className={cssObj.cssObjTamplate.input_about_templet}
                                                    onChange={(e) => { setReveiwsTitle(e.target.value) }}
                                                    title={showState ? "" : "Change Reveiws title"}
                                                    value={reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}
                                                    placeholder="Reveiws"
                                                    id={showState ? "about_p" : "about"}
                                                ></input>}
                                        </Typography>
                                    </AccordionSummary>
                                    <AccordionDetails className="rounded" style={{ background: "transperent", color: "black" }}>
                                        <Typography component={'span'} style={{ width: "100%" }}>

                                            <Reveiws
                                               {...this.props}
                                            >
                                            </Reveiws>
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion> : ""}
                        </div>

                        <div className="row  d-flex justify-content-center p-2" id="DivIdToScroll"
                            style={cssObj ? cssObj.bodyCardMobile.background_footer ? cssObj.bodyCardMobile.background_footer : null : null}
                            onClick={(e) => { e.stopPropagation(); setFooterStyleToggle(); }}
                        >
                            {card.currentCard.vCardStyle.titleFooterShow ?
                                <div className=" row mt-4 d-flex justify-content-center">
                                    {!showState || !preview ? <input
                                        className="d-flex justify-content-center rounded border-0 "
                                        placeholder=""
                                        value={cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ? cssObj.bodyCardMobile.footerMobile.titleFooter : ""}
                                        onChange={(e) => this.props.changeTitleFooterMobile(e.target.value)}
                                        style={cssObj ? cssObj.bodyCardMobile.input_footer_title ? cssObj.bodyCardMobile.input_footer_title : null : null}
                                    ></input> : <label
                                        className="d-flex justify-content-center rounded border-0 "
                                        style={cssObj ? cssObj.bodyCardMobile.input_footer_title ? cssObj.bodyCardMobile.input_footer_title : null : null}
                                    >
                                            {cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ? cssObj.bodyCardMobile.footerMobile.titleFooter : ""}
                                        </label>}

                                </div> : ""}

                            {card.currentCard.vCardStyle.fullNameFooterShow ?
                                <input
                                    autoComplete="new-password"
                                    id="fellowName" className="rounded border-0 row mt-2 p-3"
                                    style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                    placeholder="Full name"
                                    value={card.currentCard.fullNameFooter !== "" ? card.currentCard.fullNameFooter : ""}
                                    onChange={(e) => this.props.changeFullNameFooterMobile(e.target.value)}

                                ></input> : ""}
                            {card.currentCard.vCardStyle.personalEmailFooterShow ?
                                <input
                                    autoComplete="new-password"
                                    id="fellowEmail" className="rounded border-0 row mt-2 p-3"
                                    style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                    placeholder="Email"
                                    value={card.currentCard.personalEmailFooter !== "" ? card.currentCard.personalEmailFooter : ""}
                                    onChange={(e) => this.props.changePersonalEmailFooterMobile(e.target.value)}
                                    type="email"
                                // onClick={setFooterStyleToggle}

                                ></input> : ""}
                            {card.currentCard.vCardStyle.mobileFooterShow ?
                                <input
                                    autoComplete="new-password"
                                    id="fellowMobile" className="rounded border-0 row mt-2 p-3"
                                    style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                    placeholder="Mobile"
                                    value={card.currentCard.mobileFooter !== "" ? card.currentCard.mobileFooter : ""}
                                    onChange={(e) => this.props.changeMobileFooterMobile(e.target.value)}
                                    type="tel"
                                // onClick={setFooterStyleToggle}

                                ></input> : ""}

                            {card.currentCard.vCardStyle.messageFooterShow ?
                                <div className="rounded border-0 row mt-2 d-flex justify-content-center" style={{ width: "92%" }}>
                                    <textarea id="massegeBody" className="rounded row p-3"
                                        style={cssObj ? cssObj.bodyCardMobile.message_footer_background ? cssObj.bodyCardMobile.message_footer_background : null : null}
                                        value={card.currentCard.messageFooter !== "" ? card.currentCard.messageFooter : ""}
                                        onChange={(e) => this.props.changeMessageFooterMobile(e.target.value)}
                                        // onClick={setFooterStyleToggle}
                                        placeholder="Message..."
                                    ></textarea>
                                </div> : ""}
                            <div className="row  
                                        rounded d-flex justify-content-around mt-2"
                                style={{
                                    width: "92%"
                                }}
                            >
                                <div className="d-flex justify-content-center rounded mb-2" onClick={showState || preview ? this.props.sendEmail : null}
                                    style={cssObj ? cssObj.bodyCardMobile.button_footer ? cssObj.bodyCardMobile.button_footer : null : null}
                                >
                                    {!showState && !preview ?
                                        <input
                                            autoComplete="new-password"
                                            className="rounded border-0" style={{ width: "30%" }}
                                            type="text"
                                            hidden={!card.currentCard.vCardStyle.buttonFooterShow}
                                            value={cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ? cssObj.bodyCardMobile.footerMobile.buttonFooter : ""}
                                            onChange={(e) => this.props.changeButtonFooterMobile(e.target.value)}
                                            style={cssObj ? cssObj.bodyCardMobile.input_footer_button ? cssObj.bodyCardMobile.input_footer_button : null : null}
                                        >
                                        </input> : <label
                                            className="rounded border-0" style={{ width: "30%" }}
                                            style={cssObj ? cssObj.bodyCardMobile.input_footer_button ? cssObj.bodyCardMobile.input_footer_button : null : null}

                                        >  {cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ? cssObj.bodyCardMobile.footerMobile.buttonFooter : ""}
                                        </label>}

                                </div>
                            </div>
                        </div>

                    </div>
                </div>



            </>
        );
    }
}



export default BodyMpbile;