

import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import IconButton from '@material-ui/core/IconButton';
import StarBorderIcon from '@material-ui/icons/StarBorder';

const mapStateToProps = (state) => {
  return {
    gallery: state.gallerySlide.currentGallery,
    gridGallery: state.gallerySlide.currentGallery.grid,
    screenSize: state.card.screenSize
  };
}

const mapDispatchToProps = (dispatch) => ({
  setCols: (num) => dispatch(actions.setCols(num)),
})

export default connect(mapStateToProps, mapDispatchToProps)(function ImageGridList(props) {
  const { gallery, gridGallery, screenSize, showState, preview } = props;
  var height = 0, w = parseInt(window.innerWidth);
  if (!showState || preview) {
    if (screenSize === 1)
      height = gridGallery.cellHeightDesktop;
    else
      height = gridGallery.cellHeight;
  }
  else {
    if (w >= 600)
      height = gridGallery.cellHeightDesktop;
    else
      height = gridGallery.cellHeight;
  }

  return (
    <div style={gridGallery.useStyles.root}>
      {gallery.currentGallery ?
        <GridList style={gridGallery.useStyles.gridList} cols={gridGallery.cols} cellHeight={height}>
          {gallery.currentGallery.map((tile) => (
            <GridListTile key={tile.img} cols={tile.featured ? 2 : 1} rows={tile.featured ? 2 : 1}>
              <img src={tile.img} alt={tile.title} style={{ border: "1px solid " + gallery.borderColor }}
              />
            </GridListTile>
          ))}
        </GridList> : ""}
    </div >
  );
});