import React from 'react';
import SocialMediaCard from './SocialMediaCard'
import { connect } from 'react-redux';
// import { actions } from '../Redux/actions/Action';
import './Card.css'
import '../../Css/default_card_designs.css'
import { actions } from '../../Redux/actions/Action'
import SlideGallery from '../Gallery/SlideGallery'
import ImageGridList from '../Gallery/GridGallery'
import ReveiwsDesktop from '../Reveiws/ReveiwsDesktop'



class BodyDesktop extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            aboutStatus: 0, //0-close 1-aboutMe 2-aboutUs
            fellowName: "",
            fellowEmail: "",
            fellowMobile: "",
            massegeBody: "",
            titleFooter: "",
        };
    }

    setFellowDetails = (e) => {
        switch (e.target.id) {
            case "fellowName": {
                this.setState({ fellowName: e.target.value }); break;
            }
            case "fellowEmail": {
                this.setState({ fellowEmail: e.target.value }); break;
            }
            case "fellowMobile": {
                this.setState({ fellowMobile: e.target.value }); break;
            }
            case "massegeBody": {
                this.setState({ massegeBody: e.target.value }); break;
            }
            case "titleFooter": {
                this.setState({ titleFooter: e.target.value }); break;
            }
        }
    }


    render() {
        var num = 10, rows = [];
        const { showState, cssObj,
            agentDetails, card, changeFullName, changeRule, setAgentDetailsToggle, reveiw, setReveiwsToggle,
            setSocialMediaToggle, gallery, setvCardStyleToggle, setPersonalmassegeConnect, setAboutConnect, setAbouttitle, setFooterStyleToggle, preview, setReveiwsTitle } = this.props;

        return (
            <>

                <div className="row"
                    style={cssObj ? cssObj.body_card_desktop.body ? cssObj.body_card_desktop.body : null : null} >
                    <div style={cssObj ? cssObj.body_card_desktop.body_card_desktop_inline ? {
                        background:  cssObj.body_card_desktop.body_card_desktop_inline.background,
                        minHeight: cssObj.body_card_desktop.body_card_desktop_inline.minHeight,
                        width: cssObj.body_card_desktop.body_card_desktop_inline.width,
                        marginTop: cssObj.body_card_desktop.body_card_desktop_inline.marginTop,
                        borderRadius:cssObj.body_card_desktop.body_card_desktop_inline.borderRadius,
                        zIndex:0,
                    } : null : null}
                        onClick={setvCardStyleToggle}
                        title={showState ? "" : "Change body color"}
                    >

                        <div className="col d-flex flex-column  p-0" style={cssObj ? cssObj.body_card_desktop.body_card_desktop_inline_conect ? cssObj.body_card_desktop.body_card_desktop_inline_conect : null : null}>
                            {!showState ? <> <input
                                autoComplete="new-password"
                                className="row m-0 p-0 input_card_desktop"
                                placeholder={agentDetails.fullName !== "" ? "" : "Name"}
                                value={agentDetails.fullName !== "" ? agentDetails.fullName : "Name"}
                                onChange={(e) => changeFullName(e.target.value)}
                                onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                hidden={!card.currentCard.vCardStyle.fullNameShow}
                                style={cssObj ? cssObj.body_card_desktop.fullname ? cssObj.body_card_desktop.fullname : null : null}
                                title={showState ? "" : "Change name"}
                                id={showState ? "fullName_p" : "fullName"}
                            ></input>
                                <input
                                    autoComplete="new-password"
                                    className="row m-0 p-0 input_card_desktop"
                                    placeholder={agentDetails.rule !== "" ? "" : "Role"}
                                    value={agentDetails.rule !== "" ? agentDetails.rule : "Role"}
                                    onChange={(e) => changeRule(e.target.value)}
                                    onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}
                                    hidden={!card.currentCard.vCardStyle.ruleShow}
                                    style={cssObj ? cssObj.body_card_desktop.role ? cssObj.body_card_desktop.role : null : null}
                                    title={showState ? "" : "Change role"}
                                    id={showState ? "role_p" : "role"}

                                ></input></> : <>
                                    {agentDetails.fullName !== "" ? <label className="row m-0 p-0 input_card_desktop"
                                        hidden={!card.currentCard.vCardStyle.fullNameShow}
                                        style={cssObj ? cssObj.body_card_desktop.fullname ? cssObj.body_card_desktop.fullname : null : null}
                                    >{agentDetails.fullName} </label> : ""}
                                    {agentDetails.rule !== "" ? <label className="row m-0 p-0 input_card_desktop"
                                        hidden={!card.currentCard.vCardStyle.ruleShow}
                                        style={cssObj ? cssObj.body_card_desktop.role ? cssObj.body_card_desktop.role : null : null}
                                    >{agentDetails.rule}</label> : ""}
                                </>
                            }

                            {card.currentCard.vCardStyle.aboutShow ? <div className="row d-flex justify-content-center" onClick={(e) => { e.stopPropagation(); setAgentDetailsToggle() }}>
                                <div className="w-75 d-flex flex-column" >
                                    {showState ? <div
                                        autoComplete="new-password"
                                        className="about_input_card_desktop">{agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : "About"}</div> : <input
                                            autoComplete="new-password"
                                            className="about_input_card_desktop"
                                            onChange={(e) => setAbouttitle(e.target.value)}
                                            value={agentDetails.aboutTitle !== "" ? agentDetails.aboutTitle : ""}
                                            placeholder="About my business"
                                            title={showState ? "" : "Change about title"}
                                            id={showState ? "about_p" : "about"}
                                        ></input>}
                                    {!showState ? <textarea className="rounded about_card_desktop"
                                        title="Write about yourself or your business"
                                        onChange={(e) => setAboutConnect(e.target.value)}
                                        value={agentDetails.aboutConnect !== "" ? agentDetails.aboutConnect
                                            : "I'm a product designer based in Tel-Aviv with experience in delivering end-to-end UX/UI design for software products."
                                        }>
                                    </textarea> : <p className="rounded about_card_desktop">
                                            {agentDetails.aboutConnect}
                                        </p>
                                    }
                                    {/* <div className="about_input_card_desktop">About Us</div>
                                    {!showState ? <textarea className="rounded about_card_desktop"
                                        onChange={(e) => setPersonalmassegeConnect(e.target.value)}
                                        value={agentDetails.personalMassegeConnect !== "" ? agentDetails.personalMassegeConnect
                                            : `About Us...`}>
                                    </textarea > : <p className="rounded about_card_desktop">
                                            {agentDetails.personalMassegeConnect}
                                        </p>} */}

                                </div>
                            </div> : ""}
                            {/* <div style={{width:"100%",margin:"auto"}}> */}
                            {gallery.galleryShow ? gallery.state === 'grid' ? <ImageGridList {...this.props}></ImageGridList> : <div style={{ width: "90%", margin: "auto" }}> <SlideGallery {...this.props}></SlideGallery></div> : ""}
                            {/* </div> */}
                            <div className="row d-flex justify-content-center" onClick={(e) => { e.stopPropagation(); setReveiwsToggle() }}>
                                <div className="w-75 d-flex flex-column about_input_card_desktop" >
                                    {showState ? <div
                                        autoComplete="new-password"
                                        className=" d-flex flex-column">{reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}</div> : <input
                                            className=" revewis_input_card_desktop  "
                                            onChange={(e) => { setReveiwsTitle(e.target.value) }}
                                            title={showState ? "" : "Change Reveiws title"}
                                            value={reveiw.titleReveiws !== "" ? reveiw.titleReveiws : ""}
                                            placeholder="Reveiws"
                                            id={showState ? "about_p" : "about"}
                                            placeholder="Reveiws"


                                        ></input>}
                                </div>
                            </div>

                            <div className="d-flex flex-column" style={{ width: "90%", margin: "auto" }}>
                                <ReveiwsDesktop
                                    {...this.props}
                                >
                                </ReveiwsDesktop>
                            </div>
                            <div className="row" style={{ marginTop: "20px" }}>
                                <div className="col d-flex justify-content-center">
                                    <div className="row w-75">
                                        {
                                            card.currentCard.socialMedias.map((socialMedia, i) => {
                                                // if (socialMedia.url && socialMedia.url !== "" && socialMedia.name !== "Whatsapp") {
                                                if (socialMedia.show === true) {
                                                    num -= 1;
                                                    return <SocialMediaCard
                                                        key={i}
                                                        showState={showState}
                                                        css={"socialMediaWidth"}
                                                        onClickFunc={!showState ? setSocialMediaToggle : null}
                                                        styleIcon={cssObj ? cssObj.body_card_desktop.socialMedia ? cssObj.body_card_desktop.socialMedia : null : null}
                                                        title={socialMedia.name}
                                                        icon={socialMedia.icon}
                                                        url={socialMedia.url}
                                                    ></SocialMediaCard>
                                                }
                                                return "";
                                            })
                                        }
                                        {num > 0 && !showState ? <SocialMediaCard
                                            css={"socialMediaWidth"}
                                            showState={showState}
                                            onClickFunc={!showState ? setSocialMediaToggle : null}
                                            styleIcon={cssObj ? cssObj.body_card_desktop.socialMedia ? cssObj.body_card_desktop.socialMedia : null : null}
                                            title={"Plus"}
                                            icon={"FaPlus"}
                                            url={``}
                                        ></SocialMediaCard> : ""}
                                        {rows.map((val) => { return val; })}

                                    </div>

                                </div>
                            </div>

                            <div className=" footerDesktop d-flex justify-content-center align-items-center"
                                style={cssObj ? cssObj.bodyCardMobile.background_footer ? cssObj.bodyCardMobile.background_footer : null : null}
                                onClick={(e) => { e.stopPropagation(); setFooterStyleToggle() }}
                            >
                                <div className="col">

                                    {card.currentCard.vCardStyle.titleFooterShow ?
                                        <div className=" row mt-4  d-flex justify-content-center" >
                                            {!showState || !preview ? <input
                                                autoComplete="new-password"
                                                className="d-flex justify-content-center rounded border-0 "
                                                placeholder=""
                                                value={cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ? cssObj.bodyCardMobile.footerMobile.titleFooter : ""}
                                                onChange={(e) => this.props.changeTitleFooterMobile(e.target.value)}
                                                style={{
                                                    background: cssObj.bodyCardMobile.input_footer_title.background,
                                                    color: cssObj.bodyCardMobile.input_footer_title.color,
                                                    textAlign: cssObj.bodyCardMobile.input_footer_title.textAlign,
                                                    font: cssObj.bodyCardMobile.input_footer_title.font,
                                                    letterSpacing: cssObj.bodyCardMobile.input_footer_title.letterSpacing,
                                                    textTransform: cssObj.bodyCardMobile.input_footer_title.textTransform,
                                                    fontSize: "30px"
                                                }}
                                                onClick={setFooterStyleToggle}
                                            ></input> : <label
                                                className="d-flex justify-content-center rounded border-0 "
                                                style={{
                                                    background: cssObj.bodyCardMobile.input_footer_title.background,
                                                    color: cssObj.bodyCardMobile.input_footer_title.color,
                                                    textAlign: cssObj.bodyCardMobile.input_footer_title.textAlign,
                                                    font: cssObj.bodyCardMobile.input_footer_title.font,
                                                    letterSpacing: cssObj.bodyCardMobile.input_footer_title.letterSpacing,
                                                    textTransform: cssObj.bodyCardMobile.input_footer_title.textTransform,
                                                    fontSize: "30px"
                                                }}
                                            >
                                                    {cssObj.bodyCardMobile.footerMobile.titleFooter !== "" ? cssObj.bodyCardMobile.footerMobile.titleFooter : ""}
                                                </label>}

                                        </div> : ""}


                                    <div className="row  mt-2 d-flex justify-content-center" style={{ width: "90%", margin: "auto" }}>
                                        {card.currentCard.vCardStyle.fullNameFooterShow ?
                                            <input id="fellowName" className="rounded border-0 col p-3 "
                                                style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                                placeholder="Full name"
                                                value={card.currentCard.fullNameFooter !== "" ? card.currentCard.fullNameFooter : ""}
                                                onChange={(e) => this.props.changeFullNameFooterMobile(e.target.value)}
                                                autoComplete="new-password"
                                            ></input> : ""}
                                        {card.currentCard.vCardStyle.personalEmailFooterShow ?
                                            <input id="fellowEmail" className="rounded border-0 col ml-1"
                                                style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                                placeholder="e mail"
                                                value={card.currentCard.personalEmailFooter !== "" ? card.currentCard.personalEmailFooter : ""}
                                                onChange={(e) => this.props.changePersonalEmailFooterMobile(e.target.value)}
                                                type="email"
                                                autoComplete="new-password"
                                            ></input> : ""}
                                        {card.currentCard.vCardStyle.mobileFooterShow ?
                                            <input id="fellowMobile" className="rounded border-0 col ml-1 p-3"
                                                style={cssObj ? cssObj.bodyCardMobile.input_footer_background ? cssObj.bodyCardMobile.input_footer_background : null : null}
                                                placeholder="Mobile"
                                                value={card.currentCard.mobileFooter !== "" ? card.currentCard.mobileFooter : ""}
                                                onChange={(e) => this.props.changeMobileFooterMobile(e.target.value)}
                                                type="tel"
                                                autoComplete="new-password"
                                            ></input> : ""}
                                    </div>
                                    {card.currentCard.vCardStyle.messageFooterShow ?
                                        <div className="rounded border-0 row mt-2 d-flex justify-content-center" style={{ width: "90%", margin: "auto" }}>
                                            <textarea id="massegeBody" className="rounded row p-3"
                                                style={{
                                                    background: cssObj.bodyCardMobile.message_footer_background.background,
                                                    width: cssObj.bodyCardMobile.message_footer_background.width,
                                                    height: "100px",
                                                }}
                                                value={card.currentCard.messageFooter !== "" ? card.currentCard.messageFooter : ""}
                                                onChange={(e) => this.props.changeMessageFooterMobile(e.target.value)}
                                                // onClick={setFooterStyleToggle}
                                                placeholder="Message..."
                                            ></textarea>
                                        </div> : ""}
                                    <div className="row  
                                        rounded d-flex justify-content-around mt-2" style={{
                                            width: "90%",
                                            margin: "auto"
                                        }}>
                                        <div className="d-flex justify-content-center rounded mb-2" onClick={showState || preview ? this.props.sendEmail : ""}
                                            style={cssObj ? cssObj.bodyCardMobile.button_footer ? cssObj.bodyCardMobile.button_footer : null : null}

                                        >
                                            {!showState && !preview ?
                                                <input
                                                    autoComplete="new-password"
                                                    className="rounded border-0"
                                                    type="text"
                                                    hidden={!card.currentCard.vCardStyle.buttonFooterShow}
                                                    value={cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ? cssObj.bodyCardMobile.footerMobile.buttonFooter : ""}
                                                    onChange={(e) => this.props.changeButtonFooterMobile(e.target.value)}
                                                    style={cssObj ? cssObj.bodyCardMobile.input_footer_button ? cssObj.bodyCardMobile.input_footer_button : null : null}
                                                >
                                                </input> : <label
                                                    className="rounded border-0"
                                                    style={cssObj ? cssObj.bodyCardMobile.input_footer_button ? cssObj.bodyCardMobile.input_footer_button : null : null}

                                                >
                                                    {cssObj.bodyCardMobile.footerMobile.buttonFooter !== "" ? cssObj.bodyCardMobile.footerMobile.buttonFooter : ""}
                                                </label>}

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>


                    </div>
                </div>


            </>
        );
    }
}
const mapStateToProps = (state) => {
    return {

    };
}

const mapDispatchToProps = (dispatch) => ({

    changeTitleFooter: (name) => dispatch(actions.setTitleFooter(name)),
    changeFullNameFooter: (name) => dispatch(actions.setFullNameFooter(name)),
    changePersonalEmailFooter: (name) => dispatch(actions.setPersonalEmailFooter(name)),
    changeMobileFooter: (name) => dispatch(actions.setMobileFooter(name)),
    changeMessageFooter: (name) => dispatch(actions.setMessageFooter(name)),
    changeButtonFooter: (name) => dispatch(actions.setButtonFooter(name)),


})
export default connect(mapStateToProps, mapDispatchToProps)(BodyDesktop);
