import React from 'react';
import MailLogo from '../../img/MailLogo.png'
import Line from '../../img/Line.png'
import './Mail.css';
import footer_mail from '../../img/footer_mail.png'
import logoknow from '../../img/logoknow.png'


export function Mail(props) {
    const { header,body} = props;
    return (
        
        <div id="mail_text">
            <div style={{ display: "flex", flexWrap: " wrap", marginRight: " -15px", }}>
                <img src={logoknow} style={{ backgroundSize: "cover", width: "323px", height: "74px", marginLeft: "70px", marginTop: "20px" }}></img>
            </div>
            <div style={{ display: "flex", flexWrap: " wrap", marginRight: " -15px" }}>
                <div style={{ flexBasis: "0", flexGrow: "1", maxWidth: " 100%" }}>
                    <img src={MailLogo} style={{ width: "500px", height: "400px", marginLeft: " 200px", marginTop: "40px", maxWidth: " 100%" }}></img>
                </div>
                <div style={{ flexBasis: " 0", flexGrow: "1", maxWidth: " 100%" }}>
                    <div style={{
                        textAlign: "left", font: " normal normal bold 40px/45px Roboto", letterSpacing: "0px", color: " #2B0118",
                        marginTop: "40px", marginLeft: " 50px", display: "flex", flexWrap: " wrap", marginRight: " -15px",
                    }}>{header}
                     </div>
                    <div style={{
                         textAlign: "left", font: "normal normal 300 30pt/30pt Roboto",
                        letterSpacing: " 0pt", color: "#2B0118", marginLeft: "50px", marginTop:" 20px"
                    }}>
                      {body.map((mail) => (
                         <div style={{display: "flex",flexWrap: "wrap",marginRight: "-15px"}}>{mail}</div>
                          ))} 
                    </div>
                    <div style={{ display: "flex", flexWrap: " wrap", marginRight: " -15px" }}>
                        <img style={{ maxWidth: "100%", height: " auto", width: "150px!important", border: " 0pt solid #C23181", marginTop: "20px", marginLeft: " 60px" }} src={Line}></img>
                    </div>
                    <div  style={{
                        display: "flex", flexWrap: " wrap", marginRight: " -15px", textAlign: " left", font: " normal normal bold 30pt/35pt Roboto", letterSpacing: "0pt",
                        color: " #C23181", marginLeft: "50px", marginTop: "20px" }}>Lorem ips consetetur sad 
                    </div>
                </div>
            </div>
            <img style={{ maxWidth: "100%", height: " auto", position: "fixed", left: " 0", bottom: "0", width: "100%" }} src={footer_mail}></img>
        </div>


    );

};
