import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action'
import { openfileDialog } from '../../BL/StyleCard'
import UploadFile from '../helpComponents/UploadFile'


export default function CardHierOrder(Component) {
    return connect(mapStateToProps, mapDispatchToProps)(class CardHierOrder extends React.Component {
        constructor(props) {

            super(props);

        }

        changeImg = (event, id) => {
            event.stopPropagation();
            openfileDialog(id, this.props.setvCardStyleToggle);
        }

        render() {
            const { changeCoverStyle, changeLogoStyle, changeProfileStyle, setCoverReveiwsStyle, changeGlleryStyle, showState, alertStatusePreview } = this.props
            return (
                <>
                    {/* cover */}
                    <UploadFile
                        id={"fileLoaderReveiws"}
                        changeCoverStyle={setCoverReveiwsStyle}
                        idElementChange={"#reveiws"}
                    ></UploadFile>
                    {!showState ? <>
                        <UploadFile
                            id={"fileLoaderCover"}
                            changeCoverStyle={changeCoverStyle}
                            idElementChange={"#cover"}
                        ></UploadFile>
                        {/* logo */}
                        <UploadFile
                            id={"fileLoaderLogo"}
                            changeCoverStyle={changeLogoStyle}
                            idElementChange={"#logo"}
                        ></UploadFile>

                        {/* profile */}
                        <UploadFile
                            id={"fileLoaderProfile"}
                            changeCoverStyle={changeProfileStyle}
                            idElementChange={"#profile"}
                        ></UploadFile></> : ""}

                    <Component
                        changeImg={this.changeImg}
                        showState={false}
                        {...this.props}
                    //  card={preview?window.card?window.card:card:card }
                    //  agentDetails={preview?window.agentDetails?window.agentDetails:agentDetails:agentDetails }
                    ></Component>
                </>
            );

        }
    })

}
const mapStateToProps = (state) => {
    return {
        agentDetails: state.agentDetails.currentAgentForCard,
        card: state.card,
        cssObj: state.card.currentCard.vCardStyle,
        screenSize: state.card.screenSize,
        alertStatusePreview: state.alert.alertStatusePreview,
        gallery: state.gallerySlide.currentGallery,
        reveiw: state.reveiws.currentReviews,
        tamplate2:state.tamplate.tamplate2,

    };
}
const mapDispatchToProps = (dispatch) => ({
    changeFullName: (name) => dispatch(actions.setFullname(name)),

    changeRule: (rule) => dispatch(actions.setRule(rule)),

    changeMobileTitle: (mobileTitle) => dispatch(actions.setMobiletitle(mobileTitle)),

    changeCoverStyle: (style) => dispatch(actions.setCovercardStyle(style)),

    setAboutConnect: (content) => dispatch(actions.setAboutConnect(content)),

    setPersonalmassegeConnect: (content) => dispatch(actions.setPersonalmassegeConnect(content)),

    setAgentDetailsToggle: () => dispatch(actions.setAgentDetailsToggle("card")),

    setSocialMediaToggle: () => dispatch(actions.setSocialMediaToggle("card")),

    setvCardStyleToggle: () => dispatch(actions.setvCardStyleToggle("card")),

    setFooterStyleToggle: () => dispatch(actions.setFooterStyleToggle("card")),

    changeLogoStyle: (style) => dispatch(actions.setLogocardStyle(style)),

    changeProfileStyle: (style) => dispatch(actions.setProfilecardStyle(style)),

    setvCardStyleToggle: () => dispatch(actions.setvCardStyleToggle("card")),

    sendEmail: () => dispatch(actions.sendEmail()),

    setAbouttitle: (content) => dispatch(actions.setAbouttitle(content)),

    setFooterSendEmailShowMobile: (action) => dispatch(actions.setFooterSendEmailShowMobile(action)),

    changeTitleFooterMobile: (name) => dispatch(actions.setTitleFooterMobile(name)),

    changeFullNameFooterMobile: (name) => dispatch(actions.setFullNameFooter(name)),

    changePersonalEmailFooterMobile: (name) => dispatch(actions.setPersonalEmailFooter(name)),

    changeMobileFooterMobile: (name) => dispatch(actions.setMobileFooter(name)),

    changeMessageFooterMobile: (name) => dispatch(actions.setMessageFooter(name)),

    changeButtonFooterMobile: (name) => dispatch(actions.setButtonFooterMobile(name)),

    openTextAboutMe: () => dispatch(actions.openTextAboutMe()),

    setExpanded: (expanded) => dispatch(actions.setExpanded(expanded)),

    setGalleryShow: (stateShow) => dispatch(actions.setGalleryShow(stateShow)),
    
    setReveiwShow: (stateShow) => dispatch(actions.setReveiwShow(stateShow)),

    setGallerytitle: (title) => dispatch(actions.setGallerytitle(title)),
    
    setReveiwsTitle: (title) => dispatch(actions.setReveiwsTitle(title)),

    setGalleryStyleToggle: () => dispatch(actions.setGalleryStyleToggle()),

    setCoverReveiwsStyle: (style) => dispatch(actions.setCoverReveiwsStyle(style)),

    setReveiwsToggle: () => dispatch(actions.setReveiwsToggle("card")),

    setImgModalShow:(status) => dispatch(actions.setImgModalShow(status)),

    setImgId:(imgId) => dispatch(actions.setImgId(imgId)),

    setFunc:(func) => dispatch(actions.setFunc(func))

})

