import React from 'react';
import { actions } from '../../Redux/actions/Action'
import { connect } from 'react-redux';
import { openfileDialog } from '../../BL/StyleCard'
import './Reveiws.css'
import { newReview } from '../../BL/Restarts'
import Carousel from 'react-elastic-carousel';

function mapStateToProps(state) {

    return {
        screenSize: state.card.screenSize,
        reveiw: state.reveiws.currentReviews
    };
}
function mapDispatchToProps(dispatch) {
    return {
        setFullNameReveiws: (name) => dispatch(actions.setFullNameReveiws(name)),
        setRuleReveiws: (name) => dispatch(actions.setRuleReveiws(name)),
        setMessageReveiws: (name) => dispatch(actions.setMessageReveiws(name)),
        setButtonReveiws: (name) => dispatch(actions.setButtonReveiws(name)),
        setIndexReveiws: (name) => dispatch(actions.setIndexReveiws(name)),
        addNewReveiws: (name) => dispatch(actions.addNewReveiws(name)),
        setReveiwsToggle: () => dispatch(actions.setReveiwsToggle("card")),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(function ReveiwsDesktop(props) {
    const { reveiw, showState, preview, setFullNameReveiws, setRuleReveiws, setMessageReveiws,
        setReveiwsToggle, setIndexReveiws, addNewReveiws, screenSize, setReveiwsTitle } = props;
    const onChangeReveiws = (Index) => {
      
        setIndexReveiws(Index);
    }
    return (

        <div className="row mt-2 d-flex justify-content-center">
            {reveiw.reveiwsList.map((reveiwsList, i) => {
                let index = i;
                return <div key={i} onClick={!showState ? (e) => {
                    onChangeReveiws(index); e.stopPropagation(); setReveiwsToggle();
                } : ""} className="col-5 borderReveiwsBodydesktop m-1 "
                >
                    <div className="row d-flex justify-content-center">
                        <div className="col-3 p-3">
                            <div
                                className="defult__profile_reveiws " id="profile"
                                style={{
                                    background: reveiwsList.reveiwsStyle.profile_reveiws.background,
                                    backgroundSize: reveiwsList.reveiwsStyle.profile_reveiws.backgroundSize,
                                    position: reveiwsList.reveiwsStyle.profile_reveiws.position,
                                    width: "68px",
                                    height: "68px",
                                    borderRadius: reveiwsList.reveiwsStyle.profile_reveiws.borderRadius,
                                    zIndex: 1,
                                }}
                                onClick={!showState ? () => { openfileDialog("#fileLoaderReveiws") } : ""}
                            >
                            </div>
                        </div>

                        <div className="col-9 p-4">
                            {!preview && !showState ?
                                <input
                                    autoComplete="new-password"
                                    className=" m-0 p-0 input_reveiws "
                                    placeholder={reveiwsList.reveiwsDetails.full_name !== "" ? "" : "Recommend Name"}
                                    value={reveiwsList.reveiwsDetails.full_name !== "" ? reveiwsList.reveiwsDetails.full_name : "Recommend Name"}
                                    onChange={(e) => { setFullNameReveiws(e.target.value); }}
                                    onClick={(e) => { onChangeReveiws(index); }}
                                    style={{
                                        color: reveiwsList.reveiwsStyle.input_card_fullName.color,
                                        // textAlign: "center",
                                        font: reveiwsList.reveiwsStyle.input_card_fullName.font,
                                        letterSpacing: "0px",
                                        textTransform: reveiwsList.reveiwsStyle.input_card_fullName.textTransform,
                                        fontSize: "18px",
                                    }}
                                    title={preview || showState ? "" : "Change Name"}
                                // id={showState ? "fullName_p" : "fullName"}
                                ></input>
                                : <b><label className=" m-0 p-0 input_reveiws d-flex align-items-start justify-content-start"
                                    style={{
                                        color: reveiwsList.reveiwsStyle.input_card_fullName.color,
                                        // textAlign: "center",
                                        font: reveiwsList.reveiwsStyle.input_card_fullName.font,
                                        letterSpacing: "0px",
                                        textTransform: reveiwsList.reveiwsStyle.input_card_fullName.textTransform,
                                        fontSize: "18px",
                                    }}
                                >{reveiwsList.reveiwsDetails.full_name !== "" ? reveiwsList.reveiwsDetails.full_name : "Name"}</label></b>}
                            {!preview && !showState ?
                                reveiw.roleShow ?
                                    <input
                                        autoComplete="new-password "
                                        className="p-0 input_reveiws "
                                        placeholder={reveiwsList.reveiwsDetails.role !== "" ? "" : "Profession"}
                                        value={reveiwsList.reveiwsDetails.role !== "" ? reveiwsList.reveiwsDetails.role : "Profession"}
                                        onChange={(e) => { setRuleReveiws(e.target.value) }}
                                        onClick={(e) => { onChangeReveiws(index); }}
                                        hidden={!reveiw.roleShow}
                                        style={{
                                            color: reveiwsList.reveiwsStyle.input_card_rule.color,
                                            font: reveiwsList.reveiwsStyle.input_card_rule.font,
                                            letterSpacing: reveiwsList.reveiwsStyle.input_card_rule.letterSpacing,
                                            fontSize: "14px",
                                            marginTop: "0px",
                                        }}
                                        title={preview || showState ? "" : "Change role"}
                                    // id={showState ? "role_p" : "role"}
                                    ></input> : ""
                                : <label className=" m-0 p-0 input_reveiws "
                                    style={{
                                        color: reveiwsList.reveiwsStyle.input_card_rule.color,
                                        font: reveiwsList.reveiwsStyle.input_card_rule.font,
                                        letterSpacing: reveiwsList.reveiwsStyle.input_card_rule.letterSpacing,
                                        fontSize: "14px",
                                        marginTop: "0px",
                                    }}

                                >{reveiwsList.reveiwsDetails.role !== "" ? reveiwsList.reveiwsDetails.role : "Profession"}</label>}
                        </div>
                    </div>
                    {
                        !preview && !showState ?
                            <div className="row rounded d-flex flex-column pb-2">
                                <textarea id="massegeBody" className="rounded textReveiws pl-3 pr-3 pb-3"
                                    style={{
                                        background: reveiwsList.reveiwsStyle.message_reveiws_background.background,
                                        width: reveiwsList.reveiwsStyle.message_reveiws_background.width,
                                        fontSize: "13px",
                                        border: reveiwsList.reveiwsStyle.message_reveiws_background.border,
                                        color: reveiwsList.reveiwsStyle.message_reveiws_background.color,
                                        lineHeight: reveiwsList.reveiwsStyle.message_reveiws_background.lineHeight,
                                        height: reveiwsList.reveiwsStyle.message_reveiws_background.height,

                                    }}
                                    value={reveiwsList.reveiwsDetails.message_reveiws}
                                    onChange={(e) => setMessageReveiws(e.target.value)}
                                    // onClick={setFooterStyleToggle}
                                    placeholder="Message..."
                                    title={preview || showState ? "" : "Change Message"}
                                >
                                </textarea>
                            </div> : <div className="d-flex justify-content-center overflow-auto" style={{
                                background: reveiwsList.reveiwsStyle.message_reveiws_background.background,
                                width: reveiwsList.reveiwsStyle.message_reveiws_background.width,
                                fontSize: "13px",
                                border: reveiwsList.reveiwsStyle.message_reveiws_background.border,
                                color: reveiwsList.reveiwsStyle.message_reveiws_background.color,
                                lineHeight: reveiwsList.reveiwsStyle.message_reveiws_background.lineHeight,
                                height: reveiwsList.reveiwsStyle.message_reveiws_background.height,

                            }}
                            >{reveiwsList.reveiwsDetails.message_reveiws}</div>
                    }

                </div>
            })}

            {
                !preview && !showState ?

                    <button className="col-5 w-75 buttonReveiws m-1" onClick={(e) => { addNewReveiws(); }}>
                        <label className="d-flex justify-content-center" style={{ color: "#7E7D80", font: "700 18px/26px Roboto", }}>Add Review +</label></button>

                    : ""
            }
        </div >
    );
});



