// One row with scroll
import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'
import '../../Css/Card.css'



const mapStateToProps = (state) => {
    return {
        gridGallery: state.gallerySlide.currentGallery.grid,
        screenSize: state.card.screenSize
    };
}

const mapDispatchToProps = (dispatch) => ({
    setCols: (num) => dispatch(actions.setCols(num)),
    setGridOneRowWithScroll: (cheak) => dispatch(actions.setGridOneRowWithScroll(cheak)),
    setCellHeight:(height) => dispatch(actions.setCellHeight(height)),
    setCellHeightDesktop:(height) => dispatch(actions.setCellHeightDesktop(height))
})

export default connect(mapStateToProps, mapDispatchToProps)(function GridGalleryNav(props) {

    const { gridGallery,screenSize,setCols,setCellHeight,setCellHeightDesktop,setGridOneRowWithScroll } = props;
    return (
        <>
              <div className="row input_footer mb-2"  >
                <div className="col-8 p-1">
                    <label id="aboutMe" className="title" style={{ textAlign: "center" }}>row with scroll</label>
                </div>
                <div className="col-4 p-0 d-flex justify-content-end">
                    <OnOff
                        cheak={gridGallery.useStyles.gridList.flexWrap==='nowrap'?true:false}
                        onChangeOnOff={setGridOneRowWithScroll}
                        type={"OneRowWithScroll"}
                        idElement="#OneRowWithScroll"

                    ></OnOff>
                </div>
            </div>
            <div className="row mt-3 mb-2">
                <div className="col m-0 p-0 title">Num of cols</div>
                <input type="number" className="col-2 m-0 p-0 input "
                    min="1"
                    max="8"
                    value={gridGallery.cols}
                    onChange={(e) => setCols(e.target.value)}
                ></input>
            </div>
            <div className="row mt-3 mb-2">
                <div className="col m-0 p-0 title">Cell height</div>
                <input type="number" className="col-2 m-0 p-0 input "
                    // min="50"
                    value={screenSize===1?gridGallery.cellHeightDesktop:gridGallery.cellHeight}
                    onChange={(e) => screenSize===1?setCellHeightDesktop(e.target.value):setCellHeight(e.target.value)}
                ></input>
            </div>
          
        </>
    );
})