import produce from 'immer';
import createReducer from "./reducerUtils";

const initialState = {
    currentGallery: {
        title: 'Gallery',
        currentIndex:-1,
        galleryShow: true,
        borderColor: "transperent",
        state: "grid",//slide
        grid: {
            cols: 4,
            cellHeight: 50,
            cellHeightDesktop: 150,
            useStyles: {
                root: {
                    width:"90%",
                    margin:"auto",
                    flexWrap: 'wrap',
                    justifyContent: 'space-around',
                    overflow: 'auto',
                    backgroundColor: 'transparent'
                },
                gridList: {
                    flexWrap: 'nowrap',
                    transform: 'translateZ(0)',
                },
            }
        },
        slide: {
            lazyLoad: false,
            showThumbnails: true,
            thumbnailPosition: "",
            showPlayButton: true,
            showBullets: true,
            showNav: true,
            thumbnailPosition: 'bottom',
            slideDuration: 150,
            slideInterval: 1000,
            autoPlay: true,
            animation: "slide",
            timeout: 500,
            checkedRadio: true,
        },
        currentGallery: []
    }
};
const gallerySlide = {
    setCurrentGallery(state, action) {
        state.currentGallery = action.payload;
    },
    setGalleryShow(state, action) {
        state.currentGallery.galleryShow = action.payload;
    },
    setGallerytitle(state, action) {
        state.currentGallery.title = action.payload;
    },
    setcurrentIndex(state, action) {
        state.currentGallery.currentIndex = action.payload;
    },
    setGalleryCangeImages(state,action) {
        state.currentGallery.currentGallery[state.currentGallery.currentIndex].img = action.payload.url;
        state.currentGallery.currentGallery[state.currentGallery.currentIndex].thumbnail = action.payload.url;
    },
    setStateGallery(state, action) {
        state.currentGallery.state = action.payload;
    },
    setBorderColorImage(state, action) {
        state.currentGallery.borderColor = action.payload;
    },
    addImgGallery(state, action) {
        var img = {
            img: action.payload.url,
            thumbnail: action.payload.url,
        }
        state.currentGallery.currentGallery.push(img);
    },
    setThumbnails(state, action) {

        state.currentGallery.slide.showThumbnails = action.payload;
    },

    setShowThumbnails(state, action) {
        state.currentGallery.slide.showThumbnails = !state.currentGallery.slide.showThumbnails;
    },

    setAutoPlay(state, action) {
        state.currentGallery.slide.showPlayButton = !state.currentGallery.slide.showPlayButton;
    },

    setBullets(state, action) {

        state.currentGallery.slide.showBullets = action.payload;
    },

    setShowBullets(state, action) {
        state.currentGallery.slide.showBullets = !state.currentGallery.slide.showBullets;
    },

    setNavigation(state, action) {

        state.currentGallery.slide.showNav = action.payload;
    },

    setShowNavigation(state, action) {
        state.currentGallery.slide.showNav = !state.currentGallery.slide.showNav;
    },

    setSlideDuration(state, action) {
        state.slide.slideDuration = parseInt(action.payload);
    },

    setslideInterval(state, action) {
        state.currentGallery.slide.slideInterval = parseInt(action.payload);
    },

    setAnimation(state, action) {
        state.currentGallery.slide.animation = action.payload;
    },

    setTimeout(state, action) {

        state.currentGallery.slide.timeout = parseInt(action.payload);

    },

    setCheckedRadio(state, action) {

        state.currentGallery.slide.checkedRadio = !state.currentGallery.slide.checkedRadio;
    },
    setHeightGallery(state, action)
    {
        state.currentGallery.slide.height = action.payload;
    },
    // grid
    setCols(state, action) {
        state.currentGallery.grid.cols = action.payload;
    },

    setGridOneRowWithScroll(state, action) {
        if (action.payload)
            state.currentGallery.grid.useStyles.gridList.flexWrap = 'nowrap';
        else
            state.currentGallery.grid.useStyles.gridList.flexWrap = 'inherit';
    },
    setCellHeight(state, action) {
        state.currentGallery.grid.cellHeight = action.payload;
        state.currentGallery.grid.cellHeightDesktop=action.payload*3;
    },
    setCellHeightDesktop(state, action) {
        state.currentGallery.grid.cellHeight = action.payload/3;
        state.currentGallery.grid.cellHeightDesktop=action.payload;
    },
}
export default produce((state, action) => createReducer(state, action, gallerySlide), initialState);
