import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from 'react-redux';
import { API_URL_BASE_CLIENT } from '../config';

import DeleteIcon from '@material-ui/icons/Delete';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import { actions } from '../Redux/actions/Action';
import { newCard } from '../BL/Restarts';
import AlertDelete from './alert/AlertDelete';
import '../Css/Cards.css'

import '../custom.css'
import '../Css/Cards.css'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";



class Cards extends Component {
    constructor(props) {
        super(props);
        this.state = {
            numCard: 0,


        };

    }
    getEndCards = (num, rows) => {
        for (let i = num; i >= (num >= 10 ? (num - 10) : 0); i--) {
            rows.push(<> <Link key={i} to={`/${this.props.userName}`} className="col-2 d-flex flex-column align-items-stretch">
                <button style={{ background: this.props.cards[i].vCardStyle.cover.backgroundImage, backgroundSize: "cover", opacity: "0.5" }}
                    className="card_button text-center d-flex align-items-center justify-content-center border rounded-0"
                    onClick={() => this.props.fillCurrentCard(i)}
                >
                    <div className="rounded-circle border border-dark" style={{ background: this.props.cards[i].vCardStyle.logo.backgroundImage, backgroundSize: "cover" }}>

                    </div>
                    <FontAwesomeIcon title="Go to view" color="blue" onClick={(e) => {
                        e.stopPropagation();
                        window.open(`${API_URL_BASE_CLIENT}/card/${this.props.uid}/${this.props.cards[i]._id}`, "_blank")
                    }} icon={['fas', 'external-link-alt']}></FontAwesomeIcon>

                </button>
                <div className="text-center">
                    {this.props.cards[i].agentDetails.fullName}
                </div>
                <div className="row d-flex justify-content-center">
                    <DeleteIcon className="CardIcon"
                   
                   onClick={(e) => {e.preventDefault(); this.props.setalertStatuseDelete(true);this.props.setindexDelet(i)}}
                    
                    ></DeleteIcon>

                    <FileCopyIcon className="CardIcon" onClick={(e) => { e.preventDefault(); this.props.fillCurrentCard(i); this.props.addNewCurrentCard() }}></FileCopyIcon>
                </div>
                
                {/* (e) => { e.preventDefault(); this.props.deleteCard(i)} */}
            </Link>
            {this.props.alertStatuseDelete === true || this.props.alertStatuseSucsses === true ? <AlertDelete  index={i}></AlertDelete> : ""} 

            </>);
            
        }


    }
    render() {
     
       
        var num = this.props.cards ? (this.props.cards.length - 1) : 0, rows = [];
        return (
            <div className="container-fluid h-25 " >
                <div className="row h-100 m-2">
                    <div className="col-2 d-flex flex-column">
                        <button className=" add_new_button text-center d-flex align-items-center justify-content-center border rounded-0"
                            onClick={() => this.props.createNewCard(newCard())}>
                            <svg xmlns="http://www.w3.org/2000/svg" width="40" height="40" viewBox="0 0 16 16">
                                <g fill="#FFFF"><path d="M1.143,38.286H6.286V33.143A1.143,1.143,0,0,1,7.429,32H8.571a1.143,1.143,0,0,1,1.143,1.143v5.143h5.143A1.143,1.143,0,0,1,16,39.429v1.143a1.143,1.143,0,0,1-1.143,1.143H9.714v5.143A1.143,1.143,0,0,1,8.571,48H7.429a1.143,1.143,0,0,1-1.143-1.143V41.714H1.143A1.143,1.143,0,0,1,0,40.571V39.429A1.143,1.143,0,0,1,1.143,38.286Z" transform="translate(0 -32)" /></g></svg>
                        </button>
                        <label className="text-center">
                            Add New
                        </label>
                    </div>
                 
                  
                  

                    {this.props.cards && num >= 0 ? this.getEndCards(num, rows) : ""}
                    {rows.map((val) => { return val; })}

                </div>


            </div >

        );
    }
}

const mapStateToProps = (state) => {
    return {
        cards: state.card.cards,
        uid:state.user.userId,
        publishStatus: state.card.publishStatus,
        userName: state.user.userName,
        alertStatuseDelete: state.alert.alertStatuseDelete,
        alertStatuseSucsses: state.alert.alertStatuseSucsses,

    };
}

const mapDispatchToProps = (dispatch) => ({

    fillCurrentCard: (index) => dispatch(actions.setCard(index)),

    createNewCard: (newCard) => dispatch(actions.setNewCard(newCard)),

    updateCard: () => dispatch(actions.updateCard()),

    addNewCurrentCard: () => dispatch(actions.saveCard()),

    deleteCard: (insdex) => dispatch(actions.deleteCard(insdex)),

    setindexDelet: (status) => dispatch(actions.setindexDelet(status)),

    setalertStatuseDelete: (status) => dispatch(actions.setalertStatuseDelete(status)),
    
    setalertStatuseSucsses: (status) => dispatch(actions.setalertStatuseSucsses(status)),

})

export default connect(mapStateToProps, mapDispatchToProps)(Cards);









