import React, { Component } from 'react';
import './custom.css'
import { Provider } from 'react-redux';
import { API_URL_BASE_CLIENT } from './config';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import store from './Redux/Stors/AgentStore';
import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { fas } from '@fortawesome/free-solid-svg-icons'
import NewWrap from './components/Frame/NewWrap'
import Card from './components/card/Card'
import Demo from './Modals/UploadImg/uploadFile'
import { connect } from 'react-redux';
import CardPreview from './components/card/CardPreview';

library.add(fab, fas)
class App extends Component {
  static displayName = App.name;


  render() {
    return (
      <div className="container-fluid p-0 m-0  ">
        <Provider store={store}>
          <Router>
            <Switch>
              {/* view */}
              <Route path={`/card/:uid/:cardId`}>
                <div id="View" className="container-fluid " style={{ height: "100vh", overflowY: "auto", background: "rgb(235, 234, 234)" }}>
                  <Card showState={true}//view
                  ></Card>
                </div>
              </Route>

              {/* preview */}
              <Route path={`/preview/:screenSize`}>

                <div className="container-fluid " style={{ height: "100vh", overflow: "auto" }}>
                  <CardPreview {...this.props}></CardPreview>
                </div>
              </Route>

              <Route path="/:userName">
                <NewWrap></NewWrap>
                <Demo></Demo>
                {/* <Mail header={"Hello Wellcom"} body={["atara friman","05331889992","23456867@gmail.com","I'm a product designer based in Tel-Aviv with experience in delivering end-to-end UX/UI design for sof products"]}></Mail>  */}

                {/* <Galleryt></Galleryt>*/}
                {/* <div style={{
                  // https://box.leader.codes/img/Group%2019050.png
                 
                  backgroundImage:  `url(${"http://i54.tinypic.com/4zuxif.jpg"})`,
                  // backgroundImage: "url(" + "https://leader.codes/assets/img/leader_favicon.png?auto=compress&cs=tinysrgb&h=350" + ")",
                  // backgroundImage: "url(" + "https://med.zira1.com/e918d24e-4963-4389-8aa4-a5bb5feea651_5ad71acece.jpeg" + ")",
                  backgroundPosition: 'center',
                  backgroundSize: 'cover',
                  backgroundRepeat: 'no-repeat',
                  width: "500px",
                  height: "500px"

                }}>yuijhgftgyhujiuhgtfrtgyhuj</div> */}
                {/* <Ex></Ex> */}
              </Route>
              <Route path='/' component={() => {
                window.location.href = `${API_URL_BASE_CLIENT}/login`;
                return null;
              }} />

            </Switch>

          </Router>

        </Provider>

      </div >

    );
  }
}
const mapStateToProps = (state) => {
  return {
    screenSize: state.card.screenSize,
    card: state.card
  };
}
export default connect(mapStateToProps)(App);