import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { IconPickerItem } from 'react-fa-icon-picker'
import './Card.css'
import '../../Css/default_card_designs.css'




class SocialMediaCard extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { showState, setFooterSendEmailShowMobile, css, onClickFunc, styleIcon, title, icon, url } = this.props;
        return (
            !showState ? <div className={css} onClick={(e) => { e.stopPropagation(); onClickFunc() }}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { e.stopPropagation(); setFooterSendEmailShowMobile() } : null}
                        style={styleIcon}
                        title={title}
                        className=" d-flex align-items-center justify-content-center icon"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""}style={{cursor: "pointer"}} />
                    </div>
                </div>
            </div> : icon === "FaWhatsapp" ? <a className={css+" d-flex align-items-start justify-content-center"} href={`https://wa.me/${url}?text=I%20want%20to%20talk%20with%20you`}>‏
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div>
            </a>: icon === "FaPhoneAlt" ? <a className={css} href={url}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div> </a> : url ? <a className={css} onClick={() => {
                var sendUrl = url
                if (!url.startsWith("https://"))
                    sendUrl = "https://" + url
                window.open(sendUrl, "_blank")
            }}>
                <div className="row d-flex justify-content-center">
                    <div
                        onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                        style={styleIcon}
                        title={title}
                        className="d-flex align-items-center justify-content-center"
                    >
                        <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                    </div>
                </div>
            </a> : <div className={css} >
                            <div className="row d-flex justify-content-center aaa">
                                <div
                                    onClick={setFooterSendEmailShowMobile ? (e) => { setFooterSendEmailShowMobile() } : ""}
                                    style={styleIcon}
                                    title={title}
                                    className="d-flex align-items-center justify-content-center"
                                >
                                    <IconPickerItem icon={icon} color={styleIcon ? styleIcon.borderColor ? styleIcon.borderColor : "" : ""} />

                                </div>
                            </div>
                        </div>
        );
    }
}
export default SocialMediaCard;