import React from 'react';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import { OnOff } from '../helpComponents/OnOff'
import StyleInputCard from '../helpComponents/StyleInputCard'
import IconShapStyle from '../helpComponents/IconShapStyle'
import AgentDetail from '../helpComponents/AgentDetail'
import './Reveiws.css'

function mapStateToProps(state) {

  return {
    reveiw: state.reveiws.currentReviews
  };
}
function mapDispatchToProps(dispatch) {
  return {
    changeRoleReveiwsShow: (cheaked, socialMediaType) => dispatch(actions.setRoleReveiwsShow(cheaked)),
    setButtonReveiws: (name) => dispatch(actions.setButtonReveiws(name)),
    setFullNameReveiws: (name) => dispatch(actions.setFullNameReveiws(name)),
    setRuleReveiws: (name) => dispatch(actions.setRuleReveiws(name)),
    setMessageReveiws: (name) => dispatch(actions.setMessageReveiws(name)),
    setReveiwsTitle: (title) => dispatch(actions.setReveiwsTitle(title)),
    setReveiwShow: (stateShow) => dispatch(actions.setReveiwShow(stateShow)),
    setCoverReveiwsStyle: (style) => dispatch(actions.setCoverReveiwsStyle(style)),

  }
};

export default connect(mapStateToProps, mapDispatchToProps)(function ReveiwsNav(props) {
  const { reveiw, setFullNameReveiws, changeRoleReveiwsShow, setRuleReveiws, setMessageReveiws, setReveiwsTitle, setReveiwShow,setCoverReveiwsStyle,setImgModalShow,setImgId,setFunc} = props;
  return (
    <>

      <div className="container-fluid">
        <div className="row ">
          <label className="title"> Reveiws Details</label>
        </div>
        <div className="row ">
          <input className="col-8 p-0 input"
            value={reveiw.titleReveiws}
            onChange={(e) => { setReveiwsTitle(e.target.value) }}
          ></input>
          <div className="col-4 p-0 d-flex justify-content-end">
            <OnOff
              idElement="ReveiwsShow"
              cheak={reveiw.reveiwShow}
              onChangeOnOff={setReveiwShow}
              type={"ReveiwsShow"}
            ></OnOff>
          </div>
        </div>
        <div className="row title mt-2 ">recommender image or logo</div>
        <div className="row mb-3 ImageLoadReveiws"
          id="navCaver"
          onClick={() => { setImgModalShow(true); setImgId("#fileLoaderReveiws"); setFunc(setCoverReveiwsStyle) }}
          style={{ background: reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsStyle.cover.background, backgroundSize: "cover !importent", }}
        ></div>
        <div className="row mb-3">
          <AgentDetail
            header="recommend name"
            title={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.full_name}
            cheak={reveiw.roleShow}
            onChange={setFullNameReveiws}
            onChangeOnOff={changeRoleReveiwsShow}
            hiddenOnOff={false}
            type={"fullName"}
            idElement="#showFullNameReveiws"
          ></AgentDetail>
        </div>
        <div className="row mb-3">
          <AgentDetail
            header="profession"
            title={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.role}
            cheak={reveiw.roleShow}
            onChange={setRuleReveiws}
            onChangeOnOff={changeRoleReveiwsShow}
            // hiddenOnOff={false}
            type={"fullName"}
            idElement="#showFullNameReveiws"
          ></AgentDetail>
        </div>
      </div>
      <div className="col container">
        <div className="row title mb-2">recommendation Content</div>
        <div className="row d-flex flex-column textReveiws">
          <div className="form-group p-0 m-0" >
            <textarea className="form-control p-0  m-0 rounded textAgentDetailsTextArea " id="exampleFormControlTextarea2"
              onChange={(e) => { setMessageReveiws(e.target.value); }}
              value={reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.message_reveiws}
              placeholder={"Message..."}
              style={{
                height: "150px"
              }}
            >
              {reveiw.reveiwsList[reveiw.IndexReveiws].reveiwsDetails.message_reveiws}
            </textarea>
          </div>
        </div>
      </div>
    </>
  );

});
