export const styles = theme => ({
  bachdrop: {
    background: "#5a63632b"

  },
  cropContainer: {
    position: 'relative',
    width: '100%',
    height: 200,
    background: '#f4efef',
    [theme.breakpoints.up('sm')]: {
      height: 400,
    },
  },
  cropButton: {
    color: '#fff',
    backgroundColor: '#3f51b5',
    display: 'inline-flex',
    justifyContent: 'center',
    alignItems:'center',
    height: '38px',
    minWidth: '64px',
    borderRadius: '5px',
    fontFamily: 'Roboto", "Helvetica", "Arial", sans-serif',
    fontWeight: '500',
    lineHeight: 1.75,
    borderRadius: '4px',
    letterSpacing: '0.02857em',
    textTransform: 'uppercase',
    flexShrink: 0,
     marginLeft: 16
  },
  controls: {
    padding: 16,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'stretch',
    [theme.breakpoints.up('sm')]: {
      flexDirection: 'row',
      alignItems: 'center',
    },
  },
  sliderContainer: {
    display: 'flex',
    flex: '1',
    alignItems: 'center',
  },
  sliderLabel: {
    [theme.breakpoints.down('xs')]: {
      minWidth: 65,
    },
  },
  slider: {
    padding: '22px 0px',
    marginLeft: 16,
    [theme.breakpoints.up('sm')]: {
      flexDirection: 'row',
      alignItems: 'center',
      margin: '0 16px',
    },
  },
})
