import React, { useState, useCallback } from 'react'
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import Cropper from 'react-easy-crop'
import Slider from '@material-ui/core/Slider';
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core/styles'
import getCroppedImg from './cropImage'
import { styles } from './styles'
import { Modal } from 'react-bootstrap';
import { openfileDialog, convertImgToBase64, changeImgBackground } from '../../BL/StyleCard'

const Demo = (props) => {
  const { classes, imgModal, setImgModalShow, setCoverReveiwsStyle } = props;
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [rotation, setRotation] = useState(0)
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null)
  const [croppedImage, setCroppedImage] = useState(null)
  const [dogImg, setDogImg] = useState(null)
  const [event, setEvent] = useState(null)



  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels)
  }, [])

  const onClose = useCallback(() => {
    setCroppedImage(null);
    setDogImg(null);
    setImgModalShow(false);
  }, [])
  const showCroppedImage = useCallback(async () => {
    try {
      const croppedImage = await getCroppedImg(
        dogImg,
        croppedAreaPixels,
        rotation,
        event
      )
      console.log('donee', { croppedImage })
      setCroppedImage(croppedImage.url);
      setEvent(croppedImage.file);
      changeImgBackground(event, croppedImage.url, imgModal.imgId, imgModal.func,imgModal.type);
      onClose()
    } catch (e) {
      console.error(e)
    }
  }, [croppedAreaPixels, rotation])



  return (
    <>
      <input
        type="file" id="fileLoader" name="files" title="Load File" hidden
        accept="image/png, image/jpeg"
        onChange={(e) => { convertImgToBase64(e.target.files[0], setDogImg); setEvent(e.target.files[0]) }}
      />
      <Modal show={imgModal.show} onHide={() => setImgModalShow(false)} backdropClassName={classes.bachdrop} >
        <Modal.Header closeButton>
          <Modal.Title>Add background photo</Modal.Title>
        </Modal.Header>
        <Modal.Body  >
          <div  >
            <div className={classes.cropContainer}>
              <Cropper
                image={dogImg}
                crop={crop}
                rotation={rotation}
                zoom={zoom}
                aspect={3 / 3}
                onCropChange={setCrop}
                onRotationChange={setRotation}
                onCropComplete={onCropComplete}
                onZoomChange={setZoom}
              // cropSize={{height: 200,width:597 }}
              // cropShape="round"
              />
            </div>
            <div className={classes.controls}>
              <div className={classes.sliderContainer}>
                <Typography
                  variant="overline"
                  classes={{ root: classes.sliderLabel }}
                >
                  Zoom
          </Typography>
                <Slider
                  value={zoom}
                  min={1}
                  max={3}
                  step={0.1}
                  aria-labelledby="Zoom"
                  classes={{ container: classes.slider }}
                  onChange={(e, zoom) => setZoom(zoom)}
                />
              </div>
              <div className={classes.sliderContainer}>
                <Typography
                  variant="overline"
                  classes={{ root: classes.sliderLabel }}
                >
                  Rotation
          </Typography>
                <Slider
                  value={rotation}
                  min={0}
                  max={360}
                  step={1}
                  aria-labelledby="Rotation"
                  classes={{ container: classes.slider }}
                  onChange={(e, rotation) => setRotation(rotation)}
                />
              </div>

            </div>
          </div>
          <div className="d-flex justify-content-end">
            <button
              className={classes.cropButton}
              onClick={(() => openfileDialog("#fileLoader"))}
            >
              Upload Photo
            </button>
            <Button
              onClick={showCroppedImage}
              variant="contained"
              color="primary"
              classes={{ root: classes.cropButton }}
            >
              Apply
        </Button>

          </div>

        </Modal.Body>
      </Modal>
    </>
  )
}
const mapStateToProps = (state) => {
  return {
    imgModal: state.modal.imgModal
  };
}

const mapDispatchToProps = (dispatch) => ({
  setImgModalShow: (status) => dispatch(actions.setImgModalShow(status)),

  setCoverReveiwsStyle: (style) => dispatch(actions.setCoverReveiwsStyle(style)),

})
export default connect(mapStateToProps, mapDispatchToProps)(withStyles(styles)(Demo))





