import { createStore, combineReducers, applyMiddleware } from 'redux';
import { createNewCard, cardSelection, saveNewCard, updateCard, getCards, deleteCard, sendEmail, getUid } from '../MiddleWares/CardMiddleWare'
import agentDetails from '../reducers/AgentDetailsStore';
import card from '../reducers/CardStore'
import user from '../reducers/User'
import validation from '../reducers/Validation'
import alert from '../reducers/AlertStore'
import tamplate from '../reducers/TamplateStore'
import gallerySlide from '../reducers/GalleryStore'
import reveiws from '../reducers/ReveiwStore'
import modal from '../reducers/ModalStore'


// const state = {

// };

const reducer = combineReducers({ agentDetails, card, user, validation, alert, gallerySlide, tamplate,reveiws,modal});

const store = createStore(reducer, applyMiddleware(sendEmail, cardSelection, createNewCard, saveNewCard, deleteCard, updateCard, getCards, getUid));
window.store = store;
export default store;
store.dispatch({ type: 'UID_INIT' });
// store.dispatch({ type: "CARD_INIT" });

