import React, { Component } from "react";
import { actions } from '../Redux/actions/Action';
import { connect } from 'react-redux';

import StyleInputCard from './helpComponents/StyleInputCard'
import '../Css/Nav.css'




class VcarsStyleAgentDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
          
        };

    }
   
    render() {
        return (
            <div className="container-fluid mt-2 " >
                <div className="row ">
                    <label className="title">Text Style</label>
                </div>
                <div className="row ">
                    <StyleInputCard
                        title="Full Name Color"
                        background={this.props.vCardStyle.bodyCardMobile.input_card_fullName.color}
                        onchangeColor={this.props.changeFullNameTitleColor}
                        color={this.props.screenSize===0?this.props.vCardStyle.bodyCardMobile.input_card_fullName.color:this.props.vCardStyle.body_card_desktop.fullname.color}
                    > </StyleInputCard>
                </div> 

                {/* <div className="row ">
                    <StyleInputCard
                        title="Rule Color"
                        background={this.props.vCardStyle.bodyCardMobile.input_card_rule.color}
                        onchangeColor={this.props.changeRuleColor}
                        color={this.props.screenSize===0?this.props.vCardStyle.bodyCardMobile.input_card_rule.color:this.props.vCardStyle.body_card_desktop.role}
                    > </StyleInputCard>
                </div> */}
            </div >
        );
    }
}
const mapStateToProps = (state) => {
    return {
        vCardStyle: state.card.currentCard?state.card.currentCard.vCardStyle:null,
        screenSize:state.card.screenSize
    };
}

const mapDispatchToProps = (dispatch) => ({
    changeFullNameTitleColor: (color) => dispatch(actions.setFullNameTitleColor(color)),
    changeRuleColor: (color) => dispatch(actions.setRuleColor(color)),
})
export default connect(mapStateToProps, mapDispatchToProps)(VcarsStyleAgentDetails);