import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import { connect } from 'react-redux';
import { actions } from '../../Redux/actions/Action';
import './Card.css'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    button: {
        letterSpacing: "0.6px",
        color: "#FFFFFF",
        background: 'transparent',
        border: "0",
        outline: "none",
        opacity: "1",
        borderBottom: "1px solid white",
        margin: "auto !important"
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        flexBasis: '33.33%',
        flexShrink: 0,
    },
    secondaryHeading: {
        fontSize: theme.typography.pxToRem(15),
        color: theme.palette.text.secondary,
    },
}));
const mapStateToProps = (state) => {
    return {
        card: state.card
    };
}

const mapDispatchToProps = (dispatch) => ({
    setExpanded: (expanded) => dispatch(actions.setExpanded(expanded)),
})

export default connect(mapStateToProps,mapDispatchToProps)(function ControlledAccordions(props) {
    const classes = useStyles();
    const { card, setExpanded } = props;

    const handleChange = (panel) => (event, isExpanded) => {
        setExpanded({ isExpanded: isExpanded, panel: panel });
    };

    return (
        <div className="p-1" style={{ width: "100%" }}>
            <Accordion style={{
                letterSpacing: "0.6px",
                color: "#FFFFFF",
                background: 'transparent',
                border: "0",
                outline: "none",
                opacity: "1",
                borderBottom: "1px solid white",
                margin: "auto !important"
            }}
                expanded={card.expanded === 'panel1'}
                onChange={handleChange('panel1')}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                    aria-controls="panel1bh-content"
                    id="panel1bh-header"
                >
                    <Typography>General settings</Typography>
                </AccordionSummary>
                <AccordionDetails className="rounded" style={{ background: "white", color: "black" }}>
                    <Typography>
                        Nulla facilisi. Phasellus sollicitudin nulla et quam mattis feugiat. Aliquam eget
                        maximus est, id dignissim quam.
          </Typography>
                </AccordionDetails>
            </Accordion>

            <Accordion className="about_input_card" style={{ background: "transparent" }}
                expanded={card.expanded === 'panel2'} onChange={handleChange('panel2')}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon style={{ color: "white" }} />}
                    aria-controls="panel2bh-content"
                    id="panel2bh-header"
                >
                    <Typography>General settings</Typography>
                </AccordionSummary>
                <AccordionDetails style={{ background: "white" }}>
                    <Typography>
                        Nulla facilisi. Phasellus sollicitudin nulla et quam mattis feugiat. Aliquam eget
                        maximus est, id dignissim quam.
          </Typography>
                </AccordionDetails>
            </Accordion>

        </div>
    );
});
